<template>
  <div class="login">
    <div class="head-title">
        <div class="title">
          <img src="../../assets/image/realAuthorTitle.png"/>
        </div>
        <div class="subTitle">
          <p>为保证您的账号安全并顺利完成交易，请务必填写与登陆手机号相符的实名信息。</p>
          <p>当前登陆手机号：<span>138******00</span></p>
        </div>
    </div>
    <div  class="login-body">
      <van-form @submit="onSubmit" class="loginform">
        <van-field
          v-model="realname"
          name="realname"
          clearable
          clear-trigger="always"
          label=""
          @update:model-value="onInput"
          placeholder="真实姓名"
          :rules="[{ required: true, message: '请输入真实姓名' }]"
        />

        <van-field
                v-model="idCardnum"
                name="idCardnum"
                clearable
                @update:model-value="onInput"
                clear-trigger="always"
                label=""
                placeholder="本人身份证号"
                :rules="[{ required: true, message: '请输入本人身份证号' }]"
        />
        <van-field
                v-model="bankCardNum"
                name="bankCardNum"
                clearable
                clear-trigger="always"
                label=""
                placeholder="银行卡号（选填）"
        />
        <div class="tips"><van-icon name="warning"  size="14"/>
          填写的银行卡预留号码应为 138****</div>

        <div class="buttongroup">

        <div class="button getVcode" >
          <van-button  block  native-type="submit" :disabled="agreement==false||validateForm==false">
             立即认证
          </van-button>
        </div>
        <div class="agreement">
          <van-checkbox icon-size="14px"  @change="onCheckChange" v-model="agreement" >已阅读并同意  <span>《燃象实名认证协议》</span></van-checkbox>
        </div>
          <van-popover v-model:show="showPopover"
                       :closeOnClickAction="false" :overlay="false"
                       :closeOnClickOutside="false"
                       theme="dark"  placement="bottom-start" className="myPop loginPop van-popover">
            <div>请先阅读并同意协议</div>
            <template #reference>
              <div class="popoverdiv"></div>
            </template>
          </van-popover>
        <div class="back">
          <van-icon name="cross" @click="goBack"/>
        </div>
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs ,onMounted} from 'vue'
import { Toast } from 'vant'
import { useRouter } from 'vue-router'

export default {
  setup() {
    const router = useRouter();
    const state = reactive({
      realname: '',
      idCardnum: "",
      bankCardNum: "",
      showPopover:false,
      validateForm:false,
      agreement:false
    })


    const onInput=()=>{
       if(state.realname!=""&&state.idCardnum!=""){
          state.validateForm=true;
       }else{
         state.validateForm=false;
       }
    }




    const goToLoginSuccess = () => {
      router.push({ path: `/real-author-result/wait/${state.realname}` })
    }

    const onSubmit = async (values) => {
      console.log(values)
      if (state.agreement==false) {
        Toast('请先阅读并同意协议')
        return
      }

        goToLoginSuccess();

    }
    const onCheckChange = (val) => {
      state.showPopover=!val;
    }
    onMounted(async () => {
      setTimeout(()=>{
        state.showPopover=true;
      },500)

    })
    const goBack = () => {
      router.push({ path: `/home` })
    }
    return {
      ...toRefs(state),
      onSubmit,
      onInput,
      goBack,
      onCheckChange
    }
  }

}
</script>

