<template>
  <div class="login">
    <div class="head-title">
        <div class="title">
          <img src="../assets/image/title_text.png"/>
        </div>
        <div class="subTitle">
          建造你的元宇宙生活
        </div>
    </div>
    <div  class="login-body">
      <van-form @submit="onSubmit" class="loginform">
        <van-field
          v-model="phone"
          name="phone"
          clearable
          validate-trigger="onChange"
          label=""
          placeholder="请输入手机号码"
          :rules="[{validator:validatorPhone,message:'请输入正确手机号'}]"
        />
        <div class="tips"><van-icon name="warning"  size="14"/>
          新用户将自动注册并登陆</div>
        <div class="input-wrap" v-if="handleType=='submit'">
          <van-field v-for="(item,index) in list" :key="index"
                     class="cell-phone"
                  v-model="item.value"
                     type="number"
                  label=""
                     :ref="'vcode'+index"
                     @input="()=> {return onInput(index)}"
          />
        </div>
        <div class="vcodeCountDown tips" v-if="handleType=='submit'">
           <span v-if="timeout==false">{{count}}后可重新发送</span>
           <span v-else class="reloadVCode" @click="getCode"><svg-icon className="refreshicon" icon-class="refresh"></svg-icon>重新获取验证码</span>
        </div>

        <div class="button getVcode" >
          <van-button  block  native-type="submit" :disabled="(handleType=='vcode'&&(agreement==false||phonestate==false))||(handleType=='submit'&&(completeVCode==false||agreement==false))">
            {{handleType=='vcode'?'获取验证码':'确定'}}
          </van-button>
        </div>
        <div class="agreement">
            <van-checkbox icon-size="14px" v-model="agreement" @change="onCheckChange">已阅读并同意 <span>《用户协议》</span>及<span>《隐私政策》</span></van-checkbox>
        </div>

        <van-popover v-model:show="showPopover"
                     :closeOnClickAction="false" :overlay="false"
                     :closeOnClickOutside="false"
                     theme="dark"  placement="bottom-start" className="myPop loginPop van-popover">
          <div>请先阅读并同意协议</div>
          <template #reference>
            <div class="popoverdiv"></div>
          </template>
        </van-popover>


        <div class="back">
          <van-icon name="cross" @click="goBack"/>
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
import { reactive, ref, toRefs,watch,getCurrentInstance,onMounted } from 'vue'
import { Toast } from 'vant'
import { useRouter } from 'vue-router'

export default {
  setup() {
    const router = useRouter();
    const verifyRef = ref(null);
    const internalInstance = getCurrentInstance();
    const state = reactive({
      phone: '',
      code: "",
      showPopover:false,
      phonestate:false,
      list: [{
        value: ""
      }, {
        value: ""
      }, {
        value: ""
      }, {
        value: ""
      }],
      handleType:'vcode',
      completeVCode:false,
      count: 5,
      timer: null,
      timeout:false,
      agreement:false
    })
    watch(state.list,(val)=>{
      var t = val.filter((e)=> !e.value);
      0 == t.length ? state.completeVCode = true : state.completeVCode = false
    })
    // 切换登录和注册两种模式
    const toggle = (v) => {
      state.type = v
      state.verify = ''
    }
    const onInput=(e)=>{
      if(state.list[e].value && state.list[e].value.length > 0&e!=3){
        if(e < 3){
           internalInstance.refs["vcode" + (e + 1)][0].focus();
        }
        if(state.list[e].value.length > 1){
          var value = state.list[e].value;
          for(var k=e;k<state.list.length;k++){
            state.list[k].value = value[e-k];
          }

        }
      }else{
        state.list[e].value = state.list[e].value.slice(0, 1)
      }

    }
    const   getCode=()=> {
      var  t = 5;
      if(!state.timer||state.timer==null) {
        state.count = t;
        state.timeout = false;
        state.timer = setInterval((() => {
                  if (state.count > 0 && state.count <= t) {
                    state.count--
                  } else {
                    state.timeout = true;
                    clearInterval(state.timer);
                    state.timer = null
                  }
                }
        ), 1000)
      }


    }
    const goToLoginSuccess = () => {
      router.push({ path: `/login-success` })
    }

    const onSubmit = async (values) => {
      console.log(values.phone);
      if (state.agreement==false) {
        Toast('请先阅读并同意协议')
        return
      }
      if(state.handleType=='vcode'){
        state.handleType='submit';
        getCode();
      }else{
        goToLoginSuccess();
      }

    }
    const goBack = () => {
      router.push({ path: `/home` })
    }
    const onCheckChange = (val) => {
      state.showPopover=!val;
    }
    onMounted(async () => {
      setTimeout(()=>{
        state.showPopover=true;
      },500)

    })


    const validatorPhone = (val) =>{
      if(val==''){
        state.handleType='vcode';
        state.phonestate=false;
        return '请输入手机号'
      }
      let vali= /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/.test(val);

      if(vali==false){
        state.handleType='vcode';
      }
      state.phonestate=vali;
      return vali
    };
    return {
      ...toRefs(state),
      toggle,
      validatorPhone,
      onSubmit,
      verifyRef,
      onInput,
      getCode,
      goBack,
      onCheckChange
    }
  }

}
</script>
